import { createRouter, createWebHistory } from 'vue-router';

import HomeView from './views/HomeView.vue';
import ImpactoAmbientalView from './views/ImpactoAmbientalView.vue';
import TiposDeModaView from './views/TiposDeModaView.vue';
import BauruView from './views/BauruView.vue';
import SujeitoView from './views/SujeitoView.vue';

const routes = [
  { path: '/moda-circular-bauru', name: 'Home', component: HomeView },
  { path: '/moda-circular-bauru/impacto-ambiental', name: 'ImpactoAmbiental', component: ImpactoAmbientalView },
  { path: '/moda-circular-bauru/tipos-de-moda', name: 'TiposDeModa', component: TiposDeModaView },
  { path: '/moda-circular-bauru/bauru', name: 'Bauru', component: BauruView },
  { path: '/moda-circular-bauru/sujeito', name: 'Sujeito', component: SujeitoView },
];

const router = createRouter({
  // Inclua o caminho base /moda-circular-bauru/
  history: createWebHistory(import.meta.env.BASE_URL),
  routes,
});

export default router;
